.button {
  background: linear-gradient(0deg,rgba(103, 80, 164, 0.11),rgba(103, 80, 164, 0.11)),#fffbfe;
  box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15),
    0px 1px 3px rgba(0, 0, 0, 0.3);
  border-radius: 16px;
  padding: 15px 20px 16px 16px;
  gap: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
