.nav {
  margin: 30px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title {
    font-weight: 400;
    font-size: 22px;
    line-height: 28px;
    color: #323232;
  }
  &__btn-text {
    letter-spacing: 0.1px;
    color: #6750a4;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;
    margin-left: 15px;
  }
}
